<template>
  <!-- Shows whether the current restaurant is currently open and when it opens or closes next -->
  <span v-if="merchant" :class="['delivery__time', openStatusColor]">
    <i class="ico clock-ico" />
    <span>
      {{ openText }}
    </span>
  </span>
</template>

<i18n>
{
  "is": {
    "openUntil": "Opið til kl. ",
    "opensAt": "Opnar kl. ",
    "tomorrow": "á morgun"
  },
  "en": {
    "openUntil": "Open until ",
    "opensAt": "Opens at ",
    "tomorrow": "tomorrow"
  }
}
</i18n>

<script>
import openStatus from "@/mixins/openStatus";

export default {
  name: "RestaurantHours",
  mixins: [openStatus],
  computed: {
    openText() {
      const {
        isOpen,
        nextChangeDate,
        nextChangeDayStr,
        nextChangeTimeStr,
      } = this.openStatus;
      if (!nextChangeDate) return this.$t("closed");

      return `${
        isOpen ? this.$t("openUntil") : this.$t("opensAt")
      } ${nextChangeTimeStr} ${this.$t(nextChangeDayStr)}`;
    },
    openStatusColor() {
      const { isOpen, willOpenToday, nextChangeDate } = this.openStatus;
      if (!nextChangeDate) return "closed";
      return isOpen ? "isOpen" : willOpenToday ? "willOpen" : "isClosed";
    },
  },
};
</script>

<style scoped>
.isOpen {
  background-color: #aaffaa;
}
.willOpen {
  background-color: #ffffaa;
}
.isClosed {
  background-color: #aaffaa;
}

.closed {
  background-color: #ffaaaa;
}

.ico:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
}
i.clock-ico:before {
  background: url(../assets/svg/clock-blk.svg) no-repeat;
  width: 24px;
  height: 25px;
}
span .ico:before {
  margin-right: 8px;
  vertical-align: top;
}
span.delivery__time .ico:before {
  width: 16px;
  height: 16px;
  background-size: 16px auto;
}
</style>
