<template>
  <nav class="cart-restaurant__wrapper">
    <div class="cart-restaurant__button">
      <router-link to="/checkout" v-bind:class="{ disabled: !isEmpty }">
        <BaseButton class="primary btn-block" :disabled="!isEmpty">{{
          $t("checkout")
        }}</BaseButton>
      </router-link>
    </div>
    <ul class="cart-restaurant__item-wrapper" v-if="isEmpty">
      <div class="cart-restaurant__item-scroll">
        <li class v-for="item in store.items" :key="item.item_id">
          <CartItemNoImg v-bind="item" class="cart-restaurant__item" />
        </li>
      </div>
      <li
        class="cart-restaurant__item cart-restaurant__item--delivery-fee"
        v-if="store.shipment"
      >
        <div class="cart-restaurant__item-divide">
          <div class="cart-restaurant__item-left">
            <h2 class="cart-restaurant__item-name">{{ $t("deliveryFee") }}</h2>
          </div>
          <div class="cart-restaurant__item-right">
            <span class="cart-restaurant__item-price">{{
              (store.shipment.full_price * 1 || 0) | formatISK
            }}</span>
          </div>
        </div>
      </li>
      <li class="cart-restaurant__item cart-restaurant__item--total">
        <div class="cart-restaurant__item-left">
          <h2 class="cart-restaurant__item-name">{{ $t("total") }}</h2>
        </div>
        <div class="cart-restaurant__item-right">
          <span class="cart-restaurant__item-price">{{
            store.priceTotal | formatISK
          }}</span>
        </div>
      </li>
    </ul>

    <p v-else class="cart-restaurant__empty">{{ $t("emptyMsg") }}</p>
  </nav>
</template>

<i18n>
{
  "is": {
    "checkout": "Ganga frá pöntun",
    "emptyMsg": "Smelltu á rétti til að bæta þeim við pöntunina",
    "total": "Samtals",
    "deliveryFee": "Sendingargjald"
  },
  "en": {
    "checkout": "Go to Checkout",
    "emptyMsg": "Click items to add them to your order",
    "total": "Total",
    "deliveryFee": "Delivery fee"
  }
}
</i18n>

<script>
import { mapGetters } from "vuex";
import cartItemsByStore from "@/mixins/cartItemsByStore";

import BaseButton from "@/components/BaseButton";
import CartItemNoImg from "@/components/CartItemNoImg";

export default {
  name: "TheCartRestaurant",
  mixins: [cartItemsByStore],
  components: { BaseButton, CartItemNoImg },
  computed: {
    ...mapGetters(["currentStoreId"]),
    store() {
      return this.cartItemsByStore.find(
        store => store.id === this.currentStoreId
      );
    },
    isEmpty() {
      return this.store && this.store.items;
    },
  },
};
</script>

