<template>
  <AddToCartMixinWrapper
    :key="payload.isVisible"
    v-bind="itemData"
    :defaultQuantity="1"
  >
    <div slot-scope="{ updateQty, quantity, addToCart, total, adding }">
      <AddToCartPreviewModalRepresentation
        :isShowModal="payload.isVisible"
        :hideModal="hidePreviewModal"
        :imageUrl="itemData.bigImageUrl"
        :productName="productName"
        :description="itemData.short_description"
        :updateQty="updateQty"
        :quantity="quantity"
        :addToCart="() => handleAddToCart(addToCart)"
        :totalPrice="total | formatISK"
        :addingToCart="adding"
      />
    </div>
  </AddToCartMixinWrapper>
</template>

<script>
import AddToCartPreviewModalRepresentation from "@/components/AddToCartPreviewModalRepresentation";
import AddToCartMixinWrapper from "@/components/AddToCartMixinWrapper";
import { mapGetters, mapActions } from "vuex";
import { MODAL_KEY_ADD_TO_CART_PREVIEW } from "@/store/modalModule";

import getStringByLocale from "@/util/getStringByLocale";
import get from "@/util/get";

export default {
  name: "AddToCartPreviewModal",
  components: {
    AddToCartPreviewModalRepresentation,
    AddToCartMixinWrapper,
  },
  computed: {
    ...mapGetters(["getModalState"]),
    payload() {
      return this.getModalState(MODAL_KEY_ADD_TO_CART_PREVIEW);
    },
    itemData() {
      return get(this.payload, "itemData", {});
    },
    productName() {
      return getStringByLocale({
        isStr: this.itemData.name,
        enStr: this.itemData.english_name,
      });
    },
    productShortDesc() {
      return getStringByLocale({
        isStr: this.itemData.short_description,
        enStr: this.itemData.english_short_description,
      });
    },
  },
  methods: {
    ...mapActions(["hideModal"]),
    hidePreviewModal() {
      this.hideModal(MODAL_KEY_ADD_TO_CART_PREVIEW);
    },
    async handleAddToCart(addToCart) {
      await addToCart();
      this.hidePreviewModal();
    },
  },
};
</script>

<style lang="scss" scoped></style>
