<template>
  <div v-if="!isDataReady" class="grid-food-item items-grid__item">
    <div class="row row-md">
      <div class="col-sm-8 col-xs-7">
        <div class="items-grid__content">
          <h2 class="items-grid__name app-ph" />
          <div class="items-grid__desc app-ph" />
          <div class="items-grid__delivery-price app-ph" />
        </div>
      </div>
      <div class="col-sm-4 col-xs-5">
        <div class="items-grid__image app-ph" />
      </div>
    </div>
  </div>
  <div
    v-else-if="foodThumbnail"
    class="grid-food-item items-grid__item"
    @click="addToCart"
  >
    <div class="row row-md">
      <div class="col-sm-8 col-xs-7">
        <div class="items-grid__content">
          <h2 class="items-grid__name">
            <a>{{ localeName }}</a>
          </h2>
          <p class="items-grid__desc">{{ localeShortDescription }}</p>
          <span class="items-grid__delivery-price">
            {{ price | formatISK }}
          </span>
        </div>
      </div>
      <div class="col-sm-4 col-xs-5">
        <div
          v-if="foodThumbnail"
          class="items-grid__image"
          @click="handleClickProdImage"
        >
          <ImageViewer :src="foodThumbnail" loading="lazy" alt="food" />
        </div>
      </div>
    </div>
  </div>
  <div v-else class="grid-food-item items-grid__item" @click="addToCart()">
    <div class="row row-md">
      <div class="col-md-12 col-xs-12">
        <div class="items-grid__content">
          <h2 class="items-grid__name">
            <a>{{ localeName }}</a>
          </h2>
          <p class="items-grid__desc">{{ localeShortDescription }}</p>
          <span class="items-grid__delivery-price">
            {{ price | formatISK }}
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CartItemMixin from "../mixins/CartItemMixin";
import ImageViewer from "@/components/ImageViewer";
import getStringByLocale from "@/util/getStringByLocale";
import { mapActions } from "vuex";
import { MODAL_KEY_ADD_TO_CART_PREVIEW } from "@/store/modalModule";

export default {
  name: "GridFoodItem",
  mixins: [CartItemMixin],
  components: {
    ImageViewer,
  },
  props: {
    name: String,
    english_name: String,
    short_description: String,
    english_short_description: String,
    price: Number,
    image: String,
    entity_id: Number,
    parent_product_id: Number,
    isDataReady: Boolean,
    has_options: Number,
  },
  computed: {
    foodThumbnail: function() {
      if (this.image == "no_selection" || !this.image) return "";
      return (
        "https://images.aha.is/image/upload/c_thumb,g_center,h_200,w_200/products/" +
        this.image
      );
    },
    bigImageUrl: function() {
      let image = this.image;
      if (image && image !== "no_selection") {
        if (image.indexOf("https://") === -1) {
          return "https://images.aha.is/image/upload/products/" + image;
        }
        if (image.startsWith === "https://cdn.aha.is/media/catalog/product/") {
          return (
            "https://images.aha.is/image/upload/products/" +
            image.replace("https://cdn.aha.is/media/catalog/product/", "")
          );
        }
        return image;
      }
      return false;
    },
    localeName() {
      return getStringByLocale({
        enStr: this.english_name,
        isStr: this.name,
      });
    },
    localeShortDescription() {
      return getStringByLocale({
        enStr: this.english_short_description,
        isStr: this.short_description,
      });
    },
  },
  methods: {
    ...mapActions(["toggleModal"]),
    handleClickProdImage(e) {
      //if has_option === 1, just skip add to cart preview
      if (this.has_options) return;
      e.stopPropagation();
      const { toggleModal, bigImageUrl } = this;
      toggleModal({
        modalKey: MODAL_KEY_ADD_TO_CART_PREVIEW,
        itemData: {
          ...this.$props,
          bigImageUrl,
        },
      });
    },
  },
};
</script>

