<script>
import { mapActions, mapGetters } from "vuex";
import { SELLER_TYPES } from "@/util/constants";
import get from "@/util/get";
import getStringByLocale from "@/util/getStringByLocale";
import chunk from "@/util/chunk";

import getPathData from "@/mixins/getPathData";
import idTracker from "@/mixins/idTracker";
import merchant from "@/mixins/merchant";

import DeliveryBarWrapper from "@/components/DeliveryBarWrapper";
import LastOderTimeWarning from "@/components/LastOderTimeWarning";
import DeliveryCurrAddressContainer from "@/components/DeliveryCurrAddressContainer";
import GridFoodItem from "@/components/GridFoodItem.vue";
import RestaurantHours from "@/components/RestaurantHours";
import MyProductAndSameAsLastBtns from "@/components/MyProductAndSameAsLastBtns";
import AddToCartPreviewModal from "@/components/AddToCartPreviewModal";
import TheCartRestaurant from "@/components/TheCartRestaurant";
import FilterAndOptions from "@/components/FilterAndOptions";
import MyProductPopupWrapper from "@/components/MyProductPopupWrapper";
import { LOADING_KEY_FETCHING_DATA } from "@/store/loadingModule";
import { FULFILLMENT_METHOD } from "@/util/constants";

export default {
  name: "ViewRestaurant",
  mixins: [getPathData, idTracker, merchant],
  components: {
    TheCartRestaurant,
    GridFoodItem,
    RestaurantHours,
    DeliveryBarWrapper,
    DeliveryCurrAddressContainer,
    AddToCartPreviewModal,
    FilterAndOptions,
    MyProductPopupWrapper,
    LastOderTimeWarning,
    MyProductAndSameAsLastBtns,
  },
  props: {
    path: String,
  },
  data() {
    return {
      menu: {},
      shipments: [],
      restaurant: null,
      isDataReady: false,
      window: {},
      stickySide: "top",
      SELLER_TYPES,
      detectCurrSectionTimer: undefined,
      currentActiveMenuScrollTimer: null,
      activeMenu: undefined,
      fetchingDataRestaurantTimerId: null,
    };
  },
  computed: {
    ...mapGetters(["fulfillmentMethod", "userAddress"]),
    id() {
      return get(this.restaurant, "entity_id");
    },
    bannerImage: function() {
      return (
        "https://images.aha.is/image/upload/e_blur:500,f_auto,b_black,o_40,c_thumb,g_center,h_260,w_2600/products/" +
        get(this.restaurant, "image")
      );
    },
    delivery_text: function() {
      const { delivery_time, prep_time } = this.restaurant;
      let lower = Math.ceil((delivery_time + prep_time) / 10) * 10;
      return lower + " - " + (lower + 15);
    },
    currentAddress() {
      if (
        this.fulfillmentMethod === FULFILLMENT_METHOD.DELIVERY ||
        !this.supportPickupShipment
      ) {
        return this.currDeliveryText;
      }

      return get(this.merchant, "restaurant_address");
    },
    storeName() {
      return getStringByLocale({
        isStr: get(this.restaurant, "name", ""),
        enStr: get(this.restaurant, "english_name", ""),
      });
    },
  },
  methods: {
    ...mapActions(["setNavBarTitle", "toggleLoading"]),
    chunk,
    classObj(header) {
      const { activeMenu } = this;
      return {
        active:
          activeMenu && activeMenu.menu_category_id === header.menu_category_id,
        [`menuItem-${header.menu_category_id}`]: header.menu_category_id,
      };
    },
    getMenuCategory(header) {
      return getStringByLocale({
        enStr: header.english_menu_category,
        isStr: header.menu_category,
      });
    },
    getById(id) {
      return document.getElementById(id);
    },
    getByClassName(className) {
      return document.getElementsByClassName(className);
    },
    offsetTop() {
      const headerHeight = this.getByClassName("header")[0].offsetHeight;
      const categoriesElement = this.getByClassName("all-categories__menu")[0];
      const categoriesHeight = categoriesElement.classList.contains("sticky")
        ? categoriesElement.offsetHeight
        : categoriesElement.getBoundingClientRect().x;
      return headerHeight + categoriesHeight;
    },
    scrollTo(id) {
      const element = this.getById("section" + id);
      const offSetTop = this.offsetTop();
      const scrollToOffset =
        element.getBoundingClientRect().top + window.pageYOffset - offSetTop;
      window.scrollTo({ top: scrollToOffset, behavior: "smooth" });
    },
    handleMenuSticky(resizing) {
      const menuCategory = this.$refs.navSticky;
      const categoryUl = this.$refs.categoryMenu;
      const menuSticky = (menuCategory && menuCategory.offsetTop) || null;

      // handle menu sticky
      if (typeof menuSticky !== "object") {
        if (window.pageYOffset > menuSticky) {
          if (!menuCategory) return;
          // if (menuCategory.classList.contains("sticky")) return;

          menuCategory.classList && menuCategory.classList.add("sticky");
          const header = document.getElementsByClassName("header")[0];
          const marginTop = header.offsetTop + header.offsetHeight;
          menuCategory.style.top = `${marginTop}px`;
          if (screen.width <= 767) {
            categoryUl && categoryUl.classList.remove("container");
          } else {
            categoryUl && categoryUl.classList.add("container");
          }
        } else if (menuCategory && menuCategory.classList.contains("sticky")) {
          menuCategory.classList && menuCategory.classList.remove("sticky");
          menuCategory.style.top = "0px";
          categoryUl && categoryUl.classList.remove("container");
        }
      }

      // handle restaurant sticky
      const cartRestaurant = this.$refs.restaurantCart;
      if (typeof menuSticky !== "object" && window.screen.width > 1024) {
        if (window.scrollY > menuSticky) {
          if (!cartRestaurant) return;
          if (cartRestaurant.classList.contains("sticky") && !resizing) return;

          cartRestaurant.classList && cartRestaurant.classList.add("sticky");
          const header = document.getElementsByClassName("header")[0];
          const marginTop =
            header.offsetTop + header.offsetHeight + menuCategory.offsetHeight;
          cartRestaurant.style.top = `${marginTop}px`;
        } else if (
          cartRestaurant &&
          cartRestaurant.classList.contains("sticky")
        ) {
          cartRestaurant.classList && cartRestaurant.classList.remove("sticky");
          cartRestaurant.style.top = "0px";
        }
      }
    },
    detectCurrSection() {
      if (this.isDataReady) {
        const menuCategory = this.$refs.navSticky;
        clearTimeout(this.detectCurrSectionTimer);
        this.detectCurrSectionTimer = setTimeout(() => {
          const { menu, getById, getByClassName } = this;
          for (const menuItem of Object.values(menu)) {
            const domId = `section${menuItem.menu_category_id}`;

            const { top, height } =
              getById(domId) && getById(domId).getBoundingClientRect();
            const stone = this.offsetTop();
            if (top + height - 50 >= stone) {
              this.activeMenu = menuItem;
              if (
                menuCategory &&
                menuCategory.classList.contains("sticky") &&
                screen.width <= 767
              ) {
                clearTimeout(this.currentActiveMenuScrollTimer);
                this.currentActiveMenuScrollTimer = setTimeout(() => {
                  let activeSection = getByClassName(
                    `active menuItem-${menuItem.menu_category_id}`
                  )[0];
                  activeSection &&
                    activeSection.scrollIntoView({
                      behavior: "smooth",
                      inline: "center",
                    });
                }, 200);
              }
              break;
            }
          }
        }, 200);
        this.handleMenuSticky();
      }
    },
  },
  created() {
    this.toggleLoading({
      loadingKey: LOADING_KEY_FETCHING_DATA,
      isLoading: true,
    });
  },
  async mounted() {
    window.addEventListener("resize", this.detectCurrSection.bind(this, true));
    window.addEventListener("scroll", this.detectCurrSection);
    const data = await this.getPathData(`veitingar/${this.path}`);
    this.restaurant = data[0][0]; //TODO: Divide English Icelandic

    this.setNavBarTitle({
      en: get(this.restaurant, "english_name", ""),
      is: get(this.restaurant, "name", ""),
    });

    // Loop through menu categories
    Object.keys(data[2]).forEach(key => {
      let obj = data[2][key];
      obj.items = {};
      this.menu[obj.menu_category] = obj;
    });
    Object.keys(data[1]).forEach(key => {
      let obj = data[1][key];
      obj.parent_product_id = this.restaurant.entity_id;
      if (obj.attribute_set_name == "Restaurant Shipment") {
        this.shipments.push(obj);
      } else if (obj.attribute_set_name == "Food") {
        if (typeof this.menu[obj.menu_category] === "undefined") {
          this.menu[obj.menu_category] = [];
        }
        this.menu[obj.menu_category].items[obj.sku] = obj;
      }
    });

    clearTimeout(this.fetchingDataRestaurantTimerId);
    this.fetchingDataRestaurantTimerId = setTimeout(() => {
      this.toggleLoading({
        loadingKey: LOADING_KEY_FETCHING_DATA,
        isLoading: false,
      });
      this.isDataReady = true;
    }, 1000);
  },
  beforeDestroy() {
    window.removeEventListener(
      "resize",
      this.detectCurrSection.bind(this, true)
    );
    window.removeEventListener("scroll", this.detectCurrSection);
    clearTimeout(this.fetchingDataRestaurantTimerId);
  },
  watch: {
    userAddress(newAddress) {
      const valid = this.checkValidPostalCode(newAddress.postcode.toString());
      if (!valid) {
        return this.$router.push("/veitingar");
      }
    },
  },
};
</script>

