import openingHours from "@/mixins/openingHours";
import getNextOpenStatusChange from "@/util/getNextOpenStatusChange";

// Example output (as computed property openStatus)
//  openStatus:Object
//    isOpen:true
//    nextChange:"18:00"
//    willOpenToday:false
export default {
  name: "openStatus",
  mixins: [openingHours],
  computed: {
    openStatus() {
      const openStatus = getNextOpenStatusChange({
        openingHours: this.openingHours,
      });

      return {
        ...openStatus,
        nextChange: openStatus.nextChangeTimeStr,
      };
    },
  },
};
